@media only screen and (min-width: $sm) {
    .home {
        &__image {
            position: relative;
            opacity: 0.7;
            background-attachment: fixed;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            height: 100%;
            overflow: hidden;

            &--landing-page {
                background-image: url("../images/first.jpg");
            }
            &--team {
                background-image: url("../images/gamescom-team-edited.jpg");
                height: 80%;
            }
            &--poro {
                background-image: url("../images/second.jpg");
                height: 60%;
            }
        }
        &__box {
            position: absolute;
            left: 0;
            top: 40%;
            width: 100%;
            color: #000;

            &--panel {
                background-color: rgba(200, 200, 200, 0.7);
                border-radius: 5px;
                color: black;
                padding: 45px;
                width: 100%;
                margin: 0 auto;
            }
            &--title {
                letter-spacing: 5px;
                text-transform: uppercase;
                font: 20px "Lato";
                color: #111;
                padding-bottom: $spacing-md;
            }
            &--subtitle {
                letter-spacing: 5px;
                text-transform: uppercase;
                font: 16px "Lato", sans-serif;
                color: #111;
            }
            &--full-height {
                height: 100%;
                padding-top: 150px;
            }
        }
        &__container {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 auto;
            padding: 30px 50px;
            text-align: center;
            z-index: 1;

            &--tall {
                height: 480px;
            }

            &--light {
                background-color: white;
            }

            &--dark {
                background-color: #222;
                color: white;
            }
            &--margin {
                margin: 0 auto;
                width: 50%;
            }
        }
    }
}

@media only screen and (min-width: $md) {
    .home{
        padding-top: 160px;
    }
}

@media only screen and (min-width: $lg) {
    .home {
        &__box {
            &--title {
                width: 100%;
                font-size: 24px;
            }
        }
    }
}

@media only screen and (min-width: $xl) {
    .home {
        &__box {
            &--panel {
                padding: 45px;
                width: 80%;
            }
            &--title {
                width: 100%;
                font-size: 32px;
            }
        }
    }
}
