@media only screen and (min-width: $sm) {
    .header {
        &__info-text {
            font-size: 14px;
            color: white;
        }
        &__button {
            height: 100%;
            width: 100%;
            padding: 10px;
            cursor: pointer;
        }
        &__section {
            height: 100%;
            padding: 10px;
            display: none;

            &--active {
                display: block;
            }
        }
        &__upper-list {
            padding: 0;
            & > li{
                margin-bottom: 10px;
            }
        }
    }
}
@media only screen and (min-width: $md) {
    .header {
        display: flex;
        position: absolute;
        width: 100%;
        flex-direction: column;
        align-items: center;
        padding: 30px 20px;
        z-index: 1;
        &__section {
            display: flex;
            align-items: flex-end;
        }
        &__button {
            display: none;
        }
        &__upper-list {
            display: flex;
            margin-right: auto;
            padding: 0;
            & > li {
                margin-right: 15px;
            }
        }
        &__link {
            font-size: 24px;
            color: white;
            transition: all 0.1s ease-in;
            &:hover {
                color: $light-grey;
                text-decoration: underline;
                font-size: 26px;
            }
        }
    }
}
